<!-- 登录页面  -->
<template>
  <div style="height:100%">
    <div v-if="!isH5"
         class="dt-loading"></div>
    <van-row v-else
             class="dt-login">
      <van-row class="dt-logo">
        <van-image width="150"
                   height="150"
                   :src="require('../../assets/img/690a1cbb12a376ccffafd7cac1a33bb.png')" />
      </van-row>
      <van-row class="dt-title">
        扬州世界园艺博览会
      </van-row>
      <van-row class="dt-from">
        <van-form validate-first>
          <van-row class="dt-col"
                   type="flex"
                   justify="space-around">
            <van-col>
              <van-image class="dt-item-img"
                         width="15.5"
                         height="15.5"
                         :src="require('../../assets/img/zhanghao.png')" />
            </van-col>
            <van-col class="dt-input">
              <van-field v-model.trim="username"
                         name="用户名"
                         placeholder="用户名" />
            </van-col>
          </van-row>
          <van-row class="dt-col2"
                   type="flex"
                   justify="space-around">
            <van-col>
              <van-image class="dt-item-img"
                         width="15.5"
                         height="15.5"
                         :src="require('../../assets/img/mima.png')" />
            </van-col>
            <van-col class="dt-input">
              <van-field v-model.trim="password"
                         ref="passwd"
                         :type="see"
                         name="密码"
                         placeholder="密码">
                <template #button>
                  <van-button :class="isShow?'dt-eyeOpen':'dt-eyeClose'"
                              size="small"
                              @click="gaibian">
                  </van-button>
                </template>
              </van-field>
            </van-col>
          </van-row>
          <div>
            <van-row type="flex"
                     justify="space-around">
              <van-button class="dt-button"
                          @click="login">
                登录
              </van-button>
            </van-row>

          </div>
        </van-form>
      </van-row>
    </van-row>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { authByAccount } from "@/api/userApi";
import {
  getUserInformation, // 获取用户信息
  uidLogin,  // 登录语音
} from "@/utils/user";
import { loginAndroid, loginIOS } from "./login";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      isH5: false,
      see: 'password',//
      username: '',
      password: '',
      authCode: '',//登录成功返回码
      userInfoList: [],//用户账号信息
      roleList: [],//用户角色列表
      idsList: [],//用户所在部门Id列表
      departmentUserList: [],//部门成员列表
      isShow: false, // 小眼睛是否睁开
    }
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  async created () {
    if (window.android || window.webkit) {
      this.isH5 = false
    } else {
      this.isH5 = true
    }

    this.$toast.loading({
      duration: 0
    })
    /**
     * 人脸识别接口，下个版本部署
     */
    if (window.android) {
      await loginAndroid()
      // 跳转首页
      this.$router.replace({ name: 'Home' })
      return
    } else if (window.webkit) {
      // 跳转首页
      window.webkit.messageHandlers.Login.postMessage("");
      this.$router.replace({ name: 'Home' })
      return
    }
    this.$toast.clear()
    console.log("H5登录");
    if (localStorage.getItem("userInfo")) {
      // 用户登录后使用uid登录语音
      uidLogin();
      // 跳转首页
      this.$router.replace({ name: 'Home' })
    } else {
      if (window.android) {
        window.android.logout()
        localStorage.removeItem('userInfo')
        localStorage.removeItem('uid')
      } else if (window.webkit) {
        window.webkit.messageHandlers.logout.postMessage(null)
        localStorage.removeItem('userInfo')
        localStorage.removeItem('uid')
      }
      console.error("用户未登录");
    }
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.$refs.passwd.onClickRightIcon(this.gaibian)
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () {
    this.$toast.clear()
  }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {

    login () {
      if (!this.username) {
        this.$toast({
          message: '请输入用户名',
          position: 'top',
        });
      } else if (!this.password) {
        this.$toast({
          message: '请输入密码',
          position: 'top',
        });
      } else {
        let info = {
          account: this.username,
          password: this.password
        }
        authByAccount(info).then(response => {//账号密码登录接口
          if (response.data) {
            this.authCode = response.data.auth_code
            // 根据用户名获取用户信息
            getUserInformation(this.username)
            // 用户登录后使用uid登录语音
            uidLogin();
            // 跳转首页
            this.$router.replace({ name: 'Home' })
          } else {
            this.$toast({
              message: response.msg,
              position: 'top',
            });
          }
        })
      }
    },
    gaibian () {
      if (this.see === 'password') {
        this.see = 'text'
        this.isShow = true
      } else {
        this.see = 'password'
        this.isShow = false
      }
    },
  },
}
</script>
<style lang="scss" scoped>
//@import url(); 引入公共css类
@import '../../styles/dimens';

.dt-loading {
  height: 100%;
  background: #ffffff;
}

.dt-login {
  height: 100%;
  .dt-logo {
    padding-top: px2rem(59);
  }
  .dt-title {
    margin-top: px2rem(42.5);
    font-family: PingFang-SC-Bold;
    font-size: px2rem(24);
    font-weight: Bold;
    color: #333333;
    line-height: px2rem(23);
  }
  .dt-from {
    margin-top: px2rem(65);
    width: 100%;
  }
  .dt-button {
    margin-top: px2rem(29.5);
    color: #ffffff;
    line-height: px2rem(24);
    font-family: PingFang-SC-Regular;
    font-size: px2rem(15);
    font-weight: Regular;
    background-color: #5870f8;
    border-radius: px2rem(7);
    width: 70.7%;
    margin-left: 10%;
  }
  .dt-col {
    display: flex;
    align-items: center;
    margin-right: 7.7%;
  }
  .dt-col2 {
    display: flex;
    align-items: center;
    margin-right: 7.7%;
    margin-top: px2rem(20);
  }
  .dt-item-img {
    width: 16.6%;
    padding-left: px2rem(31.5);
  }
  .dt-input {
    height: px2rem(41);
    width: 75.7%;
    border-radius: px2rem(7);
    overflow: hidden;
    border: px2rem(1) solid #f2f2f2;
  }
  .dt-eyeOpen {
    background-image: url('../../assets/img/chakanmima.png');
    background-repeat: no-repeat;
    background-color: transparent;
    border-style: none;
    height: px2rem(20);
    width: px2rem(20);
  }
  .dt-eyeClose {
    background-image: url('../../assets/img/yingcangmima.png');
    background-repeat: no-repeat;
    background-color: transparent;
    border-style: none;
    height: px2rem(20);
    width: px2rem(20);
  }
}
@media (min-height: 600px) {
  .dt-login {
    background-image: url('../../assets/img/登录底部.png');
    background-size: 100%;
    background-repeat: no-repeat;
    background-position-y: bottom;
  }
}
</style>
